coration: none;\r\n}\r\n\r\n@each $var in (left, center, right) {\r\n  .text-#{$var} {\r\n    text-align: $var;\r\n  }\r\n}\r\n\r\n@each $colorKey, $color in $colors {\r\n  .text-#{$colorKey} {\r\n    color: $color;\r\n  }\r\n\r\n  .bg-#{$colorKey} {\r\n    background-color: $color;\r\n  }\r\n}\r\n\r\n@each $sizeKey, $size in $font-sizes {\r\n  .fs-#{$sizeKey} {\r\n    font-size: $size * $base-font-size;\r\n  }\r\n}\r\n\r\n// flex布局\r\n.d-flex {\r\n  display: flex;\r\n}\r\n\r\n.flex-column {\r\n  flex-direction: column;\r\n}\r\n\r\n.flex-1 {\r\n  flex: 1;\r\n}\r\n\r\n.flex-wrap {\r\n  flex-wrap: wrap;\r\n}\r\n\r\n@each $key, $value in $flex-jc {\r\n  .jc-#{$key} {\r\n    justify-content: $value;\r\n  }\r\n}\r\n\r\n@each $key, $value in $flex-ai {\r\n  .ai-#{$key} {\r\n    align-items: $value;\r\n  }\r\n}\r\n\r\n// margin padding\r\n// m,p\r\n@each $typeKey, $type in $spacing-types {\r\n  //m-0 p-1\r\n  @each $sizeKey, $size in $spacing-sizes {\r\n    .#{$typeKey}-#{$sizeKey} {\r\n      #{$type}: $size * $spacing-base-size;\r\n    }\r\n  }\r\n\r\n  //m-x-1 p-y-2\r\n  @each $sizeKey, $size in $spacing-sizes {\r\n    .#{$typeKey}-x-#{$sizeKey} {\r\n      #{$type}-left: $size * $spacing-base-size;\r\n      #{$type}-right: $size * $spacing-base-size;\r\n    }\r\n    .#{$typeKey}-y-#{$sizeKey} {\r\n      #{$type}-top: $size * $spacing-base-size;\r\n      #{$type}-bottom: $size * $spacing-base-size;\r\n    }\r\n  }\r\n\r\n  //m-t-1 p-b-3\r\n  @each $directionKey, $direction in $spacing-directions {\r\n    @each $sizeKey, $size in $spacing-sizes {\r\n      .#{$typeKey}-#{$directionKey}-#{$sizeKey} {\r\n        #{$type}-#{$direction}: $size * $spacing-base-size;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// 文字溢出显示省略号\r\n.text-ellipsis {\r\n  display: inline-block;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  // white-space: nowrap;\r\n  -webkit-line-clamp: 3;\r\n}\r\n","$colors: (\r\n  \"primary\": #666666,\r\n  \"grey\": #999,\r\n  \"dark\": #222,\r\n  \"nav\":rgba(148, 143, 143, 0.452), //顶部navbar\r\n  \"footer\":rgba(0, 0, 0, 0.527), //页脚\r\n  \"white\":rgba(255, 255, 255, 0.76),\r\n  \"articleBox\":rgba(111, 141, 161, 0.719), //文章卡片\r\n  \"articleBoxArticle\":rgba(83, 130, 161, 0.808), //文章卡片\r\n  \"title\":rgb(202, 230, 248) ,//文章标题\r\n  \"font\":rgb(58, 56, 56),//文章字体颜色 \r\n  \"red\":rgba(255, 0, 0, 0.479),\r\n  \"test\":rgb(250, 0, 0)\r\n);\r\n\r\n// 参考 bootstrap栅栏系统css中的col-xs-*、col-sm-*、col-md-* 的意义\r\n// .col-xs- 超小屏幕 手机 (<768px)\r\n// .col-sm- 小屏幕 平板 (≥768px)\r\n// .col-md- 中等屏幕 桌面显示器 (≥992px)\r\n// .col-lg- 大屏幕 大桌面显示器 (≥1200px)\r\n$base-font-size: 7rem;\r\n$font-sizes: (\r\n    xxs: 0.12,\r\n  //9px\r\n    xs: 0.1333,\r\n  //10px\r\n    sm: 0.16,\r\n  //12px\r\n    md: 0.1733,\r\n  //13px\r\n    lg: 0.1867,\r\n  //14px\r\n    xl: 0.2133,\r\n  //16px\r\n    xll:0.24,\r\n);\r\n\r\n$flex-jc: (\r\n  start: flex-start,\r\n  end: flex-end,\r\n  center: center,\r\n  between: space-between,\r\n  around: space-around,\r\n);\r\n\r\n$flex-ai: (\r\n  start: flex-start,\r\n  end: flex-end,\r\n  center: center,\r\n  baseline: baseline,\r\n  stretch: stretch,\r\n);\r\n\r\n$spacing-types: (\r\n  m: margin,\r\n  p: padding,\r\n);\r\n\r\n$spacing-directions: (\r\n  t: top,\r\n  r: right,\r\n  b: bottom,\r\n  l: left,\r\n);\r\n\r\n$spacing-base-size: 1rem;\r\n\r\n$spacing-sizes: (\r\n  0: 0,\r\n  1: 0.1,\r\n  2: 0.15,\r\n  3: 0.2,\r\n  4: 0.25,\r\n  5: 0.3,\r\n  6: 0.4,\r\n  7: 0.6,\r\n  8: 1,\r\n);\r\n"]}]);
