<template>
  <div v-if="show" class="loadingContainer">
    <div class="loadingMask"></div>
    <div class="loadingContent">
      <div class="loadingAnimate"></div>
      <div class="loadingText">{{ text }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      text: "正在加载..."
    };
  }
};
</script>

<style lang="scss" scoped>
.loadingContainer {
  position: relative;
  text-emphasis: center;
  .loadingMask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
  }
  .loadingContent {
    position: fixed;
    left: 50%;
    top: 45%;
    z-index: 999;
    transform: translate(-50%, -45%);
    text-align: center;
    color: #fff;
    .loadingAnimate {
      display: inline-block;
      width: 35px;
      height: 35px;
      margin: 25px 0 10px;
      vertical-align: middle;
      animation: circleLoading 1s steps(12, end) infinite;
      background: transparent
        url("data:image/svg+xml;base64,")
        no-repeat;
      background-size: 100%;
    }
    .loadingText {
      font-size: 14px;
      color: #fff;
    }
  }
}

@keyframes circleLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }
  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}
</style>